@media (max-width: 992px) {
    $animation_time : 0.3s;
    .sidebar-background {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 9800;
        transition: background-color $animation_time;
        .opened-sidebar {
            position: fixed;
            top: 0;
            width: 250px;
            height: 100%;
            background-color: blue;
            z-index: 9900;
            box-shadow: none;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }
        .left {
            left: -300px;
            transition: left $animation_time, box-shadow $animation_time;
        }
    }
    .sidebar-background-active {
        background-color: rgba(0, 0, 0, 0.2);
        transition: background-color $animation_time;
        .left {
            left: 0;
            transition: left $animation_time, box-shadow $animation_time;
        }
    }

}