.main-profile {
    width: 80vw;
    height: 60vh;
    margin: 4rem auto;
    overflow: auto;
    .about-me {
        display: flex;
        //width: 70%;
        align-items: center;
        & img {
        width: 200px;
        height: 200px;
        aspect-ratio: 1/1;
        border-radius: 50%;
        object-fit: cover;
        }
        & p {
            padding: 2rem;
        }
    }
    .contact-box {
        width: 80vw;
        display: flex;
        justify-content: space-between;
        align-items: center;
        
    & .contact-info {
        width: 50%;
        & p {
            display: flex;
            align-items: center;
            justify-content: space-between;
            span {
                cursor: pointer;
            }
            .icon {
                /* 색상 반전 */
                -webkit-filter: invert(100%); /* safari 6.0 - 9.0 */
                filter: invert(100%);
                cursor: default;
            }
        }
    }
}
    .tooltip-box {
        color: black;
        background-color: rgba($color: #ffffff, $alpha: 0.7);
        padding: 0.5rem 0.8rem;
        border-radius: 500px;
        align-items: center;
    
    }

}
.main-profile::-webkit-scrollbar {
        width: 0px;
        background: transparent;
}
#copy-tooltip {
    font-family: gaming;
    background-color: rgba($color: #ffffff, $alpha: 0.7);
    padding: 0.5rem 0.8rem;
    border-radius: 500px;
    animation: fadeout 1.5s;
    -moz-animation: fadeout 1.5s; // firefox
    -webkit-animation: fadeout 1.5s; // safari and chrome
    animation-fill-mode: forwards;
}
@keyframes fadeout {
    from { opacity: 1; }
    to { opacity: 0; }
}

/* responsive */
@media (max-width: 992px) {
    .main-profile {
        margin: 0 auto;
        height: 80vh;
        
        .about-me {
        display: block;
            & p { padding: 0; font-size: medium;}
            & img {
                display: block;
                width: 200px;
        aspect-ratio: 1/1;
            object-fit: cover;
                margin-left: auto;
                margin-right: auto;
            }
        }
        .contact-box {
            display: block;
            text-align: center;
            .contact-info {
                width: 100%;
                p { font-size: small;}
            }
            .tooltip-box {
                visibility: hidden;
            }
        }
    }
}