.main {
    // background-image: url('../../image/3.jpg');
    // background-repeat: no-repeat;
    // height: 100vh;
    // width: 100vw;
    // background-size: cover;
    & .main-box {
        height: 50vh;
        display: flex;
        align-items: center;
        .box-area { 
            display: flex;
            
        }
    }
    .character {
        background: url('../../image/small-walking.png');
        position: absolute;
        bottom: 34px;
        width: 320px;
        height: 320px;
        animation: walk 2s steps(8) infinite,
            forward 1s linear infinite;
    }
}

@keyframes walk {
    0% {
        background-position: 0px;
    }
    100% {
        background-position: 2560px;
    }
}