.main-resume {
    width: 80vw;
    height: 60vh;
    margin: 20px auto;
    overflow: auto;
    & .title-area {
        display: flex;
        align-items: center;
        justify-content: center;
        h1 { margin-right: 10px; }
        
    }
    & .icon {
        /* 색상 반전 */
        -webkit-filter: invert(100%); /* safari 6.0 - 9.0 */
        filter: invert(100%);
        cursor: pointer;
        
    }
    & .row {
        
        display: flex;
        .col-title { 
            flex: 4;
            text-align: center;
        }
        .col-content{ flex: 8;
        & p {
            display: flex;
            align-items: center;
            & img { // 스택 아이콘
            width: 50px;
            padding: 5px;
            
            }
        }
        td {
            padding: 5px;
        }
    }
    }

}
#resume-tooltip {
            font-family: gaming;
            background-color: rgba($color: #ffffff, $alpha: 0.7);
            padding: 0.5rem 0.8rem;
            border-radius: 500px;
}

.main-resume::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

/* 반응형 */
@media (max-width: 992px) {
    .main-resume {
        width: 100vw;
        & .row {
            display: block;
            .col {
                padding: 1rem;
            }
        }
    }
}