/* icon */
.icon svg {
    height: 2rem;
    width: fit-content;
    
}


/* question-box */
.question-box {
    background-color: darkgoldenrod;
    border: 1px solid black;
    width: 7rem;
    position: relative;
    text-align: center;
    aspect-ratio: 1/1;
    cursor: pointer;
}
.clicked {
    animation: bounce 0.9s linear;
}

@keyframes bounce {
    20%, 50%, 80%, to { 
        transform: translateY(0);
    }
    40% { 
        transform: translateY(-30px);
    }
    70% {
        transform: translateY(-15px);
    }
    // 90% {
    //     transform: translateY();
    // }
}

/* slider */
.slick-slider {
    width: 100%;
    overflow: hidden;
    position: relative;
    & .slick-list {
        display: flex;
        & .slick-track {
            display: flex;
            align-items: center;
            img { 
                width: 100%;
            }
        }
    }
    /* 화살표 */
    & .custom-arrow {
        width: fit-content;
        opacity: 0.3;
        position: absolute;
        z-index: 500;
        & img {
            width: 40px;
            cursor: pointer;
        }
    }
    .next-arrow {
        top: 50%;
        right: 0%;
        transform: translateY(-50%);
    }
    .prev-arrow {
        -webkit-transform: scaleX(-1);
        top: 50%;
        transform: scaleX(-1) translateY(-50%);
        
    }
    .custom-arrow:hover{
        opacity: 0.7;
    }
}

/* Project template */
.project-template {
    width: 80vw;
    color: #ffffff;
    & .row { display: flex; 
        .col1, .col2 {
            width: 50%;
            padding: 1rem;
        }
    }
    .image-area {
        margin-top: 2rem;
    }
    .stack-area img {
        width: 50px;
        padding: 5px;
    }
    .link-block a {
        text-decoration: none;
    }

    }

/** responsive */
@media (max-width : 992px ) {
    .project-template {
        width: 100vw;
        & .row{
            display: block;
            & .col1, .col2 {
                width: 100vw;
                padding: 0;
                .link-block, .stack-area, .description {
                    padding: 1rem;
                }
            }
            & .col1 > h2 {
                display: flex;
                justify-content: center;
            }
        }
        
    }
}