@font-face {
    font-family: gaming;
    src: url(../../font/RetroGaming.ttf);
}

.header {
    font-family: gaming;
    font-size: larger;
    color: white;
    .fa-solid, .fa-bars {
        padding: 10px;
    }
    & .header-navbar {
        margin: 0;
        padding: 1rem;
        display: flex;
        justify-content: space-around;
        align-items: flex-end;
        list-style-type: none;
        // 어떻게 위치를 고정할까..
        & li {
            a {
            color: white;
            text-decoration: none;
        }}
    }
}

@media (max-width: 993px) {
    .header {
        .header-navbar { visibility: hidden;}
        ul {
            list-style-type: none;
            li { padding: 1rem;}
            a { text-decoration: none; }
            .character {
                background: url('../../image/small-walking.png');
                position: absolute;
                right: -20px;
                width: 320px;
                height: 320px;
                animation: walk 2s steps(8) infinite,
                    forward 1s linear infinite;
            }

        }
    }
}
@keyframes walk {
    0% {
        background-position: 0px;
    }
    100% {
        background-position: 2560px;
    }
}